import routesComponent from './routesComponent'

export default [

  {
    path: '/home',
    name: 'home',
    component: routesComponent.homeComponent,
    children: [
      {
        path: '/musicLibrary',
        name: 'musicLibrary',
        component: routesComponent.musicLibraryComponent,
        children: [
          {
            path: '/recommend',
            name: 'recommend',
            component: routesComponent.recommendComponent
          },
          {
            path: '/songSheet',
            name: 'songSheet',
            component: routesComponent.songSheetComponent
          },
          {
            path: '/mv',
            name: 'mv',
            component: routesComponent.mvComponent
          },
          {
            path: '/radioStation',
            name: 'radioStation',
            component: routesComponent.radioStationComponent
          }
        ]
      },
      {
        path: '/find',
        name: 'find',
        component: routesComponent.findComponent
      },
      {
        path: '/mine',
        name: 'mine',
        component: routesComponent.mineComponent
      }
    ]
  },

  {
    path: '/about',
    name: 'about',
    component: routesComponent.aboutComponent
  },
  {
    path: '/album/:id',
    name: 'album',
    component: routesComponent.albumComponent
  },

  {
    path: '*',
    redirect: {name: 'recommend'}
  }

]