export const mutations = {

	//修改歌单列表数据
	songAlbumData(state, result) {

		state.albumData.data.playlist.tracks = result.data.playlist.tracks.slice(0, 12);
	},

	//修改当前播放歌曲数据
	modifySongsData(state, item) {
		state.songsData = item
		
		// console.log('state.songsData :', state.songsData);
	},

};